import Vue from 'vue'
import VueRouter from 'vue-router'
import EnterSellSaveStatistics from '../views/EnterSellSaveStatistics.vue'
import ProductInfoView from '../views/ProductInfoView.vue'
import CashCreditEntering from '../views/CashCreditEntering.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    redirect: '/enter-sell-save-tatistics'
  },
  {
    path: '/enter-sell-save-tatistics',
    name: 'EnterSellSaveStatistics',
    component: EnterSellSaveStatistics,
    meta: {
      title: '进销存统计'
    }
  },
  {
    path: '/product-info-view',
    name: 'ProductInfoView',
    component: ProductInfoView,
    meta: {
      title: '产品信息展示'
    }
  },
  {
    path: '/cash-credit-entering',
    name: 'CashCreditEntering',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: CashCreditEntering,
    meta: {
      title: '现金账录入'
    }
  }
]

const router = new VueRouter({
  routes
})
router.afterEach((to, from) => {
  console.log(to);
  const { title } = to.meta
  document.title = `煤场管理系统-${title}`
})
export default router
